var getRmb=require('../../common/requset.js')
import keyboardPackage from '../../components/keyboard-package/keyboard-package.vue';
import passwordInput from '../../components/password-input/password-input.vue';
export default {
    data() {
        return {
            name:'',
            pag:'',
            suu:'',
            data:'',
            san:'',
            sun:'',
            paymentPwd:'',
            forget:false,
            password:'',
            arr:[],
            numberList: [],
            length: 6,
            type: 'number',
            passIn: false
        }
    },
   components: {
   	keyboardPackage,
   	passwordInput
   },
    onLoad(option) {
        let arr = []
        var that = this
        var data = JSON.parse(option.ront)
        that.data = data
        var sur=that.data[1];
        that.name = that.data[0][0].name
        that.pag = that.data[0][0].mobile
        that.san = that.data[0][0].sale_num
        that.suu = that.data[0][0].sale_money
        that.data=that.data[1]   
        that.sun = getRmb.getrmb(that.suu)
        for(let i = 0; i < sur.length; i++) {
            arr.push(sur[i].machine_id)
        }
      this.arr = arr
    },
    methods:{
        clo: function() {
        	this.passIn = false;
        	this.$refs['number'].close();
        	this.numberList.length= 0;
        },
        onDelete() {
        	this.numberList.pop();
        },
        onChange(e){
        	if(e.show==false){
        		this.passIn = false
        	}
            that.numberList.length= 0;
        },
        onConfirm() {
                if(this.numberList.length!=6){
                  uni.showToast({
                    title: '请输入六位交易密码！',
                    duration: 2000,
                    icon: 'none'
                  });
                }
              },
        onInput(val) {
            var that = this
            var a = that.arr.join()
        	that.numberList.push(val);
        	that.password = that.numberList.join().replace(/,/g, '');
        	if (that.numberList.length >= that.length) {
        		uni.request({
        			url: that.url + 'submitorder/',
        			method: 'POST',
        			data: {
                        password:that.password,
                        machine_id_list:a,
                        name:that.name,
                        mobile:that.pag,
                        sale_num:that.san,
                        sale_money:that.suu
        			},
        			header: {
        				Authorization: 'JWT' + ' ' + this.global_.token
        			},
        			success(res) {
                        if(res.statusCode==400){
                            that.numberList.pop();
                            that.numberList.length= 0;
                            that.$refs.wrong.flag=false;
                            var n=res.data.data.err_num;
                            var s=5-n;
                            that.$refs.wrong.tip='剩余'+ s +'次机会';
                        }
                        if(res.statusCode==423){
                        	uni.showToast({
                        		title:'交易密码已锁定,请在今日24:00后进行交易',
                        		icon:'none',
                        		duration:2000
                        	})
                        }
                        if(res.statusCode==201){
                        	that.success=true
                        }
        				if (res.statusCode == 200) {
        					uni.showToast({
        						title: '出售成功，等待买家付款',
        						icon: 'none',
        						duration: 2000
        					});
                            uni.switchTab({
                                url:'../../pages/mill/mill'
                            })
        				}
                        if(res.statusCode == 302) {
                            uni.showModal({
                                title:'未设置交易密码',
                                confirmText:'去设置',
                                success(res) {
                                    if(res.confirm == true) {
                                        uni.navigateTo({
                                            url:'../../my/trade-password/trade-password'
                                        })
                                    }
                                }
                            })
                        }
        				var page = getCurrentPages().pop();
        				if (page == undefined || page == null) return;
        			},
        		});
                this.numberList.length= 0;
        	}
        },
        btn:function () {
            var that = this
            this.passIn = true;
            this.$refs['number'].open();
            this.onInput(val);
        }
    }
}
